import { Component, OnInit, NgProbeToken, TemplateRef } from '@angular/core';
import { Form, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { ProjectService } from '../../Services/PMProjectServices';
import { ProjectData } from 'src/app/datamodel/project';
import { UserService } from '../../Services/PMUserServices';
import { User } from 'src/app/datamodel/user';
import { Observable } from 'rxjs/Observable';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import {FormBuilder, Validators } from '@angular/forms';
import {TaskService} from '../../Services/PMTaskServices';
import { HttpErrorResponse } from '@angular/common/http';
import { BOOL_TYPE } from '@angular/compiler/src/output/output_ast';


@Component({
  selector: 'app-project',
  templateUrl: './project.component.html',
  styleUrls: ['./project.component.css']
})
export class ProjectComponent implements OnInit {

  private user_ID: number;
  private username: string;

  dataSaved = false;
  projectForm: FormGroup; 
  allProjects$: Observable<ProjectData[]>;
  routerlocal: Router; 
  selectedAssignedToDropdownList:any;
  tasksForProject=[];   
  allUsers: Observable<User[]>;
  
  private UserIdNew: string;
  private UserName: string;

  public modalRef: BsModalRef; 
  
  projectData: ProjectData = { ProjectId: null, ProjectName: '', ProjectStartDate:  new Date(), ProjectEndDate: new Date(), ProjectPriority: 0, UserId: null, Users: null,Tasks:null }

  constructor(private formBuilder:FormBuilder, private objTaskService: TaskService,private objProjectService: ProjectService,private modalService: BsModalService,private router: Router )
   {
    this.routerlocal= router; 
    this.getAllUsers(); 
    this.LoadAllProjects();  
   }

  ngOnInit()
  {

    this.projectForm = this.formBuilder.group({
      ProjectName: ['', [ Validators.required ] ],      
      ProjectPriority: ['', [ Validators.required ] ],
      ProjectStartDate:  [{value: new Date(), disabled: true}, [ Validators.required ] ],
      ProjectEndDate:  [{value: new Date(), disabled: true}, [ Validators.required ] ],
      ProjectStatus: ['', [ Validators.required ] ],     
      UserId:['', [ Validators.required ] ]
     
    });
   
    this.objProjectService.getProjects()     
    .subscribe(tasksForProject =>this.tasksForProject = tasksForProject);  
  }

  onFormSubmit() {
    this.dataSaved = false;
    let project = this.projectForm.value;
    this.objProjectService.getProjects().subscribe(projects => {
      let maxIndex = projects.length - 1;
      let maxIndexItem = projects[maxIndex];
     
      project.ProjectId = 0;
      if(project.ProjectStartDate<new Date())
        project.ProjectStartDate=new Date();
      if(project.ProjectEndDate<new Date())
        project.ProjectEndDate=new Date();
     
      this.CreateProject(project);
    });
    this.projectForm.reset();
  }
  getAllUsers(): void
  {   
    
    this.objTaskService.GetAllUsers()    
    .subscribe(usersnew =>this.allUsers = usersnew);  
    
  }
 
  CreateProject(objProject: ProjectData) {
    this.objProjectService.addProject(objProject).subscribe(
      project => {
        console.log(project);     

       if( project!=null && project.ProjectId!=-1)
       {
        this.dataSaved = true; 
        alert("Project is successfully created");    
       }
       else
       {
        this.dataSaved = false; 
        alert("Project is already exists"); 
       }
        //this.LoadAllProjects();
      },
      err => {
        console.log(err);
      }
    );
    this.LoadAllProjects();
  }
  LoadAllProjects() 
  {
    this.allProjects$ = this.objProjectService.getProjects();
  } 
 
  ProjectAssignedToDropdown(selectedAssignedTo:any)
  {
    this.selectedAssignedToDropdownList=selectedAssignedTo;
  }

 get ProjectName() 
 {
    return this.projectForm.get('ProjectName');
 }
 
 get ProjectPriority() {
    return this.projectForm.get('ProjectPriority');
 } 
 get ProjectStartDate() {
  return this.projectForm.get('ProjectStartDate');
} 
get ProjectEndDate() {
  return this.projectForm.get('ProjectEndDate');
} 
get ProjectStatus() {
  return this.projectForm.get('ProjectStatus');
} 

get UserId() {
  return this.projectForm.get('UserId');
} 


ViewProject(values:any)
{     
    this.routerlocal.navigateByUrl("/viewprojects");    
}

public chkStartEndChanged()
{   

  if( this.projectForm.get('ProjectStartDate').enabled==false)
  {
    this.projectForm.get('ProjectStartDate').enable({ onlySelf: true });
    this.projectForm.get('ProjectEndDate').enable({ onlySelf: true });
  }
  else
  {
    this.projectForm.get('ProjectStartDate').disable({ onlySelf: true });
    this.projectForm.get('ProjectEndDate').disable({ onlySelf: true });
  }

}
public openModal(template: TemplateRef<any>) {
  this.modalRef = this.modalService.show(template);// {3}
}

SaveProject() 
{
  let project:ProjectData = { ProjectId:2, ProjectName: 'Project2',
          ProjectPriority: 3,
          ProjectStartDate:new Date('01/16/2019'),ProjectEndDate:new Date('01/31/2019'),
          //ProjectStatus:'In Prpgress', 
          UserId:1,
          Users:null,
          Tasks:null
           };

     this.objProjectService.PostProject(project).subscribe(res => { 
          let projectObj: ProjectData = res.body;
          console.log(projectObj.ProjectName);
          console.log(res.headers.get('Content-Type'));		
          this.LoadAllProjects();	  
        },
	(err: HttpErrorResponse) => {
          if (err.error instanceof Error) {
            //A client-side or network error occurred.				 
            console.log('An error occurred:', err.error.message);
          } 
          else 
          {
            //Backend returns unsuccessful response codes such as 404, 500 etc.				 
            console.log('Backend returned status code: ', err.status);
            console.log('Response body:', err.error);
          }
        }
     );
}



}