import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { NewTasksComponent }  from './uicomponents/viewtasks/viewtasks.component';
import { AddNewTasksComponent }  from './uicomponents/addnewtasks/addnewtasks.component';
//import { AppLoginComponent }  from './uicomponents/login/login.component';
import { UserComponent }  from './uicomponents/user/user.component';
import { ProjectComponent }  from './uicomponents/project/project.component';
import { ReactiveFormsModule,FormsModule  } from '@angular/forms';
import { ViewUsersComponent }  from './uicomponents/ViewUsers/ViewUsers.component';
import { ViewProjectsComponent }  from './uicomponents/ViewProjects/ViewProjects.component';
import { ProjectDashBoardComponent }  from './uicomponents/ProjectDashBoard/ProjectDashBoard.Component';
import { BsDropdownModule } from 'ngx-bootstrap/dropdown';
import { TooltipModule } from 'ngx-bootstrap/tooltip';
import { ModalModule } from 'ngx-bootstrap/modal';
import { Ng2SearchPipeModule } from 'ng2-search-filter';
import {AgGridModule} from 'ag-grid-angular/main';
import { SelectDropDownModule } from 'ngx-select-dropdown'

const routes: Routes = [

  //{ path: 'login', component: AppLoginComponent }, 
  { path: 'appnewtasks', component: NewTasksComponent },  
  { path: 'addnewtasks', component: AddNewTasksComponent },
  { path: 'user', component: UserComponent },
  { path: 'viewusers', component: ViewUsersComponent },
  { path: 'viewprojects', component: ViewProjectsComponent },
  { path: 'project', component: ProjectComponent },
  { path: 'projectdashboard', component: ProjectDashBoardComponent },
  { path: '', redirectTo: '/projectdashboard', pathMatch: 'full' }  

];

@NgModule({
  imports: [
    RouterModule.forRoot(routes),
    ReactiveFormsModule,    
    FormsModule,
    Ng2SearchPipeModule,
    BsDropdownModule.forRoot(),   
    
    TooltipModule.forRoot(),
    ModalModule.forRoot(),
    AgGridModule.forRoot(),    
    RouterModule.forRoot(routes),
    SelectDropDownModule],
    
  exports: [RouterModule]
  
})
export class AppRoutingModule { }
