  Refresh()
  {
    this.projectService.getProjects()
    .subscribe(response => {
      this.projects = response;
      console.log(response);             
                     
    });    

  }
  sort(param: any) {
    if (param == 'StartDate') {
      this.projectService.getProjects()
        .subscribe(response => {
          this.projects = response.sort((a, b) => {
            return <any>new Date(b.ProjectStartDate) - <any>new Date(a.ProjectStartDate);
          });
        })
    }
    if (param == 'EndDate') {
      this.projectService.getProjects()
        .subscribe(response => {
          this.projects = response.sort((a, b) => {
            return <any>new Date(b.ProjectEndDate) - <any>new Date(a.ProjectEndDate);
          });
        })
    }
    if (param == 'Priority') {
      this.projectService.getProjects()
        .subscribe(response => {
          this.projects = response.sort((a, b) => a.ProjectPriority.toString().localeCompare(b.ProjectPriority.toString(), undefined, { numeric: true }));
        });
    }
      if (param == 'Completed') {
      this.projectService.getProjects()
         .subscribe(response => {
          this.projects = response.sort((a, b) => a.ProjectStatus.toString().localeCompare(b.ProjectStatus.toString(), undefined, { numeric: true }));
        }); 
      }
  }
  endDateAfterOrEqualValidator(formGroup:FormGroup){
    var startDateTimestamp, endDateTimestamp;
    for (var controlName in formGroup.controls) {
      if (controlName.indexOf("StartDate") !== -1) {
        startDateTimestamp = Date.parse(formGroup.controls[controlName].value);
      }
      if (controlName.indexOf("EndDate") !== -1) {
        endDateTimestamp = Date.parse(formGroup.controls[controlName].value);
      }
    }
    return (endDateTimestamp < startDateTimestamp) ? { endDateLessThanStartDate: true } : null;
  }
  compareTwoDates(form:FormGroup){
   if(new Date(form.controls['StartDate'].value)<new Date(form.controls['EndDate'].value)){
           return false;
   }
}
 
