
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Observable } from 'rxjs/Observable';
import { HttpErrorResponse } from '@angular/common/http';
import {TaskService} from '../../Services/PMTaskServices';
import { Task } from '../../DataModel/task';
import { Router} from '@angular/router';
import { User } from '../../DataModel/user';
import { ProjectData } from 'src/app/datamodel/project';
import { UserService } from '../../Services/PMUserServices';
@Component({
  selector: 'app-addnewtasks',
  templateUrl: './addnewtasks.component.html',
  styleUrls: ['./addnewtasks.component.css']
})
export class AddNewTasksComponent implements OnInit {


  dataSaved = false;
  taskForm: FormGroup; 
  allTasks$: Observable<Task[]>;
  routerlocal: Router;
  selectedTaskFromDropdownList:any;
  selectedAssignedToDropdownList:any;
  selectedProjectAssignedToDropdownList:any;
  tasksForParent=[];   
  allUsers: Observable<User[]>;
  allProjects:ProjectData[];

  constructor(private formBuilder:FormBuilder, private objTaskService: TaskService,private router: Router,private objUserService:UserService )
   { 

    this.routerlocal= router; 
    this.getAllUsers();   
    this.getAllProjects();
   }

  ngOnInit() 
  {

    this.taskForm = this.formBuilder.group({
      TaskName: ['', [ Validators.required ] ],
      TaskDescription: ['', [ Validators.required] ],
      TaskPriority: ['', [ Validators.required ] ],
      StartDate:  ['', [ Validators.required ] ],
      EndDate:  ['', [ Validators.required ] ],
      TaskStatus: ['', [ Validators.required ] ],
      ParentTaskId: ['', [ Validators.required ] ],
      TaskAssignedTo:['', [ Validators.required ] ],
      ProjectId:['', [ Validators.required ] ]

    });
  
    this.objTaskService.GetAllParentTasks()     
    .subscribe(tasksnewForParent =>this.tasksForParent = tasksnewForParent);  
  }

  onFormSubmit() {
    this.dataSaved = false;
    let task = this.taskForm.value;
    this.objTaskService.GetAllTasks().subscribe(tasks => {
      // let maxIndex = tasks.length - 1;
      // let maxIndexItem = tasks[maxIndex];
      // if (maxIndexItem==null)
      //   task.TaskId = 0;
      // else
      //   task.TaskId = parseInt(maxIndexItem.id) + 1;
      task.TaskId = 0;
      this.CreateTask(task);
    });
    this.taskForm.reset();
  }

 
  
  getAllProjects(): void
  {       
    this.objUserService.getProjects()    
    .subscribe(projectsnew =>this. allProjects = projectsnew);   
    
  }
  getAllUsers(): void
  {   
    
    this.objTaskService.GetAllUsers()    
    .subscribe(usersnew =>this.allUsers = usersnew);  
    
  }
//   doLogout(values: any)
//   {     
//         this.routerlocal.navigateByUrl("/login"); 
      
//  }
  CreateTask(task: Task) {
    this.objTaskService.AddTask(task).subscribe(
      task => {
        console.log(task);
        this.dataSaved = true;
        this.LoadAllTasks();
      },
      err => {
        console.log(err);
      }
    );
  }
  LoadAllTasks() 
  {
    this.allTasks$ = this.objTaskService.GetAllTasks();
  }  
  SelectedTaskFromDropdown(selectedTaskID:any)
  {    
    this.selectedTaskFromDropdownList=selectedTaskID;
  }
  SelectedTaskAssignedToDropdown(selectedAssignedTo:any)
  {
    this.selectedAssignedToDropdownList=selectedAssignedTo;
  }
  SelectedProjectAssignedToDropdown(selectedProjectAssignedTo:any)
  { 
    this.selectedProjectAssignedToDropdownList=selectedProjectAssignedTo;
  }
 get TaskName() 
 {
    return this.taskForm.get('TaskName');
 }
 get TaskDescription() {
    return this.taskForm.get('TaskDescription');
 }  
 get TaskPriority() {
    return this.taskForm.get('TaskPriority');
 } 
 get StartDate() {
  return this.taskForm.get('StartDate');
} 
get EndDate() {
  return this.taskForm.get('EndDate');
} 
get TaskStatus() {
  return this.taskForm.get('TaskStatus');
} 

get ParentTaskId() {
  return this.taskForm.get('ParentTaskId');
} 
get TaskAssignedTo() {
  return this.taskForm.get('TaskAssignedTo');
}
get ProjectId() {
  return this.taskForm.get('ProjectId');
}


ViewTask(values:any)
{     
    this.routerlocal.navigateByUrl("/appnewtasks");    
}
SaveTask() 
{
  let task:Task = { TaskID:2, TaskName: 'Java Functional Interface',
          TaskDescription: 'Java 8', TaskPriority: 3,
          StartDate:'01/16/2019',EndDate:'01/31/2019',
          TaskStatus:'In Prpgress', ParentTaskId:'1',TaskAssignedTo:'1',ProjectId:1
           };

     this.objTaskService.PostTask(task).subscribe(res => { 
          let taskObj: Task = res.body;
          console.log(taskObj.TaskName);
          console.log(res.headers.get('Content-Type'));		
          this.LoadAllTasks();	  
        },
	(err: HttpErrorResponse) => {
          if (err.error instanceof Error) {
            //A client-side or network error occurred.				 
            console.log('An error occurred:', err.error.message);
          } 
          else 
          {
            //Backend returns unsuccessful response codes such as 404, 500 etc.				 
            console.log('Backend returned status code: ', err.status);
            console.log('Response body:', err.error);
          }
        }
     );
}
}
