import { Component, OnInit } from '@angular/core';
import { Task } from '../../DataModel/task';
import { TASKS } from '../../DataModel/tasks';
import { ParentTask } from '../../DataModel/parenttask';
import { Observable } from "rxjs"; 
import { User } from '../../DataModel/user';
import {TaskService} from '../../Services/PMTaskServices';
//import { Injectable  } from '@angular/core';
import { Router } from '@angular/router';
import { UserService } from '../../Services/PMUserServices';
import { ProjectData } from 'src/app/datamodel/project';

@Component({
  selector: 'app-new-tasks',
  templateUrl: './viewtasks.component.html',
  styleUrls: ['./viewtasks.component.css']
})

//@Injectable({ providedIn: 'root'})
export class NewTasksComponent implements OnInit {

  tasks = TASKS; 
  prenttasks:ParentTask[];
  tasksForSeacrh=[];   
  selectedTask: Task;
  routerlocal: Router;  
  objService:TaskService; 
  objUserService:UserService;
  dataSaved = false;  
  searchedTaskName:any;
  searchedParentTaskName:any;
  searchedPriorityFrom:any;
  searchedPriorityTo:any;
  searchedStartDate:any;
  searchedEndDate:any; 

  allTasks$: Observable<Task[]>;  
  allUsers: Observable<User[]>;
  allProjects:ProjectData[];
  constructor(private router: Router,private objService1:TaskService,private objUserService1:UserService) 
  {   
    this.routerlocal= router;  
    this.objService=objService1;
    this.objUserService=objUserService1;

    this.getAllUsers();
    this.getAllParentTasks();
    this.getAllProjects();

   }

  ngOnInit()
  {    
    this.getTasks(); 
  }
  getAllProjects(): void
  {       
    this.objUserService.getProjects()    
    .subscribe(projectsnew =>this. allProjects = projectsnew);   
    
  }
  getAllUsers(): void
  {     
    this.objService.GetAllUsers()    
    .subscribe(usersnew =>this.allUsers = usersnew);     
  }
  getAllParentTasks(): void
  {       
    this.objService.GetAllParentTasks()     
    .subscribe(tasksnew =>this.prenttasks = tasksnew);   
    
  }
  getTasks(): void
  {  
    this.objService.GetAllTasks()     
    .subscribe(tasksnew =>this.tasks = tasksnew);       
  }

  EditTask(objTask: Task)
  {
   
  }
  doLogout(values: any)
  {    
         this.routerlocal.navigateByUrl("/login");            
  }

  SearchTask()
  { 
      this.ClearSelectedTaskControls();     
      this.selectedTask=null;
      this.searchedTaskName= (<HTMLInputElement>document.getElementById("tasknameforsearch")).value; 

      //this.searchedParentTaskName=(<HTMLInputElement>document.getElementById("parenttasknameforsearch")).value; 

      //this.searchedPriorityFrom=(<HTMLInputElement>document.getElementById("priorityFromforsearch")).value; 
      //this.searchedPriorityTo=(<HTMLInputElement>document.getElementById("priorityToforsearch")).value;
  
      this.searchedStartDate=(<HTMLInputElement>document.getElementById("StartDateforsearch")).value; 
      this.searchedEndDate=(<HTMLInputElement>document.getElementById("EndDateforsearch")).value;  

      this.objService.SearchTasks(this.searchedTaskName,this.searchedStartDate,this.searchedEndDate)      
      .subscribe(tasksnew =>this.tasks = tasksnew);

  }

  ClearSelectedTaskControls()
  {
    if(this.selectedTask!=null)
    {
      this.selectedTask.TaskID=0;
      this.selectedTask.TaskPriority=0;
      this.selectedTask.TaskStatus="0";
      this.selectedTask.ParentTaskId="0";  
      this.selectedTask.TaskAssignedTo="0"; 
      (<HTMLInputElement>document.getElementById("taskname")).value=""; 
      (<HTMLInputElement>document.getElementById("taskdescription")).value=""; 
      (<HTMLInputElement>document.getElementById("rngTaskPriority")).value="";
      (<HTMLInputElement>document.getElementById("taskstartdate")).value="";  
      (<HTMLInputElement>document.getElementById("taskenddate")).value="";  
      (<HTMLInputElement>document.getElementById("taskStatus")).value="0";    
      (<HTMLInputElement>document.getElementById("parentTask")).value="0"; 
      (<HTMLInputElement>document.getElementById("TaskAssignedTo")).value="0"; 
      
    }   
  }
  CancelEdit()
  { 
    this.selectedTask=null;

  }

  ClearSearchTasks()
  {
    (<HTMLInputElement>document.getElementById("tasknameforsearch")).value=""; 
    //(<HTMLInputElement>document.getElementById("parenttasknameforsearch")).value=""; 

    //(<HTMLInputElement>document.getElementById("priorityFromforsearch")).value=""; 
    //(<HTMLInputElement>document.getElementById("priorityToforsearch")).value=""; 

    (<HTMLInputElement>document.getElementById("StartDateforsearch")).value=""; 
    (<HTMLInputElement>document.getElementById("EndDateforsearch")).value="";    
    this.getTasks();  

  }

  ViewTask(values:any)
  { 
    this.ClearSelectedTaskControls();     
    this.selectedTask=null;
    this.getTasks();    
  }

  AddTask(values:any)
  {   
     this.routerlocal.navigateByUrl("/appaddnewtasks"); 
  }

  onSelect(task: Task): void
  {
    this.selectedTask = task;       

  }

  EndTask(objTask: Task)
  {    
    if(this.selectedTask!=null)
    {       
      objTask.TaskStatus="Completed";
      this.objService.UpdateTask(objTask).subscribe(
        task => {
          console.log(task);
          this.dataSaved = true;
          //this.LoadAllTasks();
          this.allTasks$ = this.objService.GetAllTasks();
          if( this.selectedTask!=undefined)
          { 
            alert("Task is completed");        
          } 
        },
        err => {
          console.log(err);
        }
      );         
    } 
  }

  DeleteTask(objTask: Task)
  {
    if (confirm("Are you sure to delete the Task : "+objTask.TaskName+" ?")==true)
    {     
      if(this.selectedTask!=null)
      {       
        objTask.TaskStatus="ToBeDeleted";
        this.objService.deleteTask(objTask).subscribe(
          task => {
            console.log(task);
            this.dataSaved = true;
            //this.LoadAllTasks();
            this.allTasks$ = this.objService.GetAllTasks();
           
            if( this.selectedTask!=undefined)
            { 
              alert("Task is deleted"); 
              this.ClearSelectedTaskControls();
                this.getTasks();  
              
            } 
          },
          err => {
            console.log(err);
          }
        );         
      }

      }     

    }
  
  UpdateTask(objTask: Task)
  {
   
   this.objService.UpdateTask(objTask).subscribe(
     task => {
       console.log(task);
       this.dataSaved = true;
       //this.LoadAllTasks();
       this.allTasks$ = this.objService.GetAllTasks();

       if( this.selectedTask!=undefined)
       { 
           alert("Task is saved");
          this.selectedTask=null;
           this.getTasks();      
       }  
     },
     err => {
       console.log(err);
     }
   );
 }
}
