import { Component, OnInit } from '@angular/core';
import { Task } from '../../DataModel/task';
import { ProjectData } from '../../DataModel/project';
import { Observable } from "rxjs"; 
import { User } from '../../DataModel/user';
import {TaskService} from '../../Services/PMTaskServices';
import {UserService} from '../../Services/PMUserServices';
import { Router } from '@angular/router';

@Component({
  selector: 'app-ViewUsers',
  templateUrl: './viewusers.component.html',
  styleUrls: ['./viewusers.component.css']
})

export class ViewUsersComponent implements OnInit {

  users : User[]; 
  projects:ProjectData[];
  tasks:Task[];

  usersForSeacrh=[];   
  selectedUser: User;
  routerlocal: Router;
  
  objUserService:UserService; 
  objTaskService:TaskService; 
  dataSaved = false;
  
  searchedFirstName:any;
  searchedLastName:any;
  searchedUserName:any;  
  searchedempid:any;

  allUsers$: Observable<User[]>;
  
  allProjects: Observable<ProjectData[]>;
  
  constructor(private router: Router,private objUserService1:UserService,objTaskService1:TaskService) 
  {   
    this.routerlocal= router;  
    this.objUserService=objUserService1;    
    this.objTaskService=objTaskService1;
    this.getAllProjects();
    this.getAllTasks();
   }

  ngOnInit()
  {    
    this.getAllUsers(); 
  }

  getAllUsers(): void
  {     
    this.objUserService.getUsers()    
    .subscribe(usersnew =>this.users = usersnew);     
  }
  getAllProjects(): void
  {       
    this.objUserService.getProjects()    
    .subscribe(projectsnew =>this.projects = projectsnew);   
    
  }
  getAllTasks(): void
  {  
    this.objTaskService.GetAllTasks()     
    .subscribe(tasksnew =>this.tasks = tasksnew);       
  }

  EditUser(objUser: User)
  {
    
  }
  DeleteUser(objUser: User)
  {
    if (confirm("Are you sure to delete the User : "+objUser.FirstName+" "+objUser.LastName+"?")==true)
    {
      if( (objUser.ProjectId==null || objUser.ProjectId==0) && (objUser.TaskId==null || objUser.TaskId==0) )
      {
       
        this.objUserService.deleteUser(objUser).subscribe(
          usertoupdated => {
            console.log(usertoupdated);
            this.dataSaved = true;
           
            this.allUsers$ = this.objUserService.getUsers();
     
            if( this.selectedUser!=undefined)
            { 
                alert("User ID : " +this.selectedUser.UserId+ " is deleted");             
                this.selectedUser=null;
                this.getAllUsers();      
            }  
          },
          err => {
            console.log(err);
          }
        );

      }
      else
      {
        alert("User canot be deleted, as user is assigned with Project/Tasks");
      }
    }    
  } 

  CancelEdit()
  { 
    this.selectedUser=null;

  }

  SearchUser()
  { 
      this.ClearSelectedUserControls();     
      this.selectedUser=null;
      this.searchedFirstName= (<HTMLInputElement>document.getElementById("firstnameforsearch")).value; 
      this.searchedLastName= (<HTMLInputElement>document.getElementById("lastnameforsearch")).value; 
      this.searchedUserName= (<HTMLInputElement>document.getElementById("usernameforsearch")).value; 
      this.searchedempid= (<HTMLInputElement>document.getElementById("empidforsearch")).value; 

      this.objUserService.SearchUsers(this.searchedFirstName,this.searchedLastName,this.searchedUserName,this.searchedempid)
      .subscribe(usersnew =>this.users = usersnew);

  }

  ClearSelectedUserControls()
  {
    if(this.selectedUser!=null)
    {
      this.selectedUser.UserId=0;
      this.selectedUser.UserName="";
      this.selectedUser.FirstName="";
      this.selectedUser.LastName="";  
      this.selectedUser.EmpId="0"; 
      this.selectedUser.ProjectId=0; 
      this.selectedUser.TaskId=0; 
      (<HTMLInputElement>document.getElementById("firstname")).value=""; 
      (<HTMLInputElement>document.getElementById("lastname")).value=""; 
      (<HTMLInputElement>document.getElementById("username")).value="";
      (<HTMLInputElement>document.getElementById("empid")).value="";  
      (<HTMLInputElement>document.getElementById("projectid")).value="0";    
      (<HTMLInputElement>document.getElementById("taskid")).value="0";       
      
    }   
  }

  ClearSearchUsers()
  {
    (<HTMLInputElement>document.getElementById("firstnameforsearch")).value=""; 
    (<HTMLInputElement>document.getElementById("lastnameforsearch")).value=""; 
    (<HTMLInputElement>document.getElementById("usernameforsearch")).value=""; 
    (<HTMLInputElement>document.getElementById("empidforsearch")).value=""; 
   
    this.getAllUsers();  
  }

  ViewUser(values:any)
  { 
    this.ClearSelectedUserControls();     
    this.selectedUser=null;
    this.getAllUsers();    
  }

  AddUser(values:any)
  {   
     this.routerlocal.navigateByUrl("/user"); 
  }

  onSelect(user: User): void
  {
    this.selectedUser = user;     

  } 

  UpdateUser(objUser: User)
  {
    
   
   this.objUserService.PostUser(objUser).subscribe(
     usertoupdated => {
       console.log(usertoupdated);
       this.dataSaved = true;
      
       this.allUsers$ = this.objUserService.getUsers();

       if( this.selectedUser!=undefined)
       { 
           alert("User ID : " +this.selectedUser.UserId+ " is saved");             
           this.getAllUsers();      
       }  
     },
     err => {
       console.log(err);
     }
   );
 }
}
