import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http'
import { ProjectData } from '../datamodel/project';
import { Observable } from 'rxjs';
import { HttpHeaders,HttpResponse   } from '@angular/common/http';
import { catchError, map, tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ProjectService {
  //For localhost
  //private apiUrl: string = 'http://localhost:50695/api/projects?type=json';

  //For Production
  private apiUrl: string = 'http://localhost:8081/Api/Project';

  constructor(private http: HttpClient) { }

  addProject(request: any): Observable<ProjectData>
  {
    
    return this.http.post<ProjectData>(this.apiUrl + '/InsertProjectDetails', request);
  }

  getProjects(): Observable<ProjectData[]>
  {    
    return this.http.get<ProjectData[]>(this.apiUrl + '/AllProjectDetails');
  }

  deleteProject(objProject: ProjectData): Observable<HttpResponse<ProjectData>> 
  {

      objProject.ProjectStatus="ToBeDeleted";
      let httpHeaders = new HttpHeaders({
      'Content-Type' : 'application/json'
     });    
      return this.http.post<ProjectData>(this.apiUrl + '/DeleteProjectDetails', objProject,
       {
            headers: httpHeaders,
           observe: 'response'
        }
      );
  }

 

  getProject(id: number): Observable<ProjectData> {
    return this.http.get<ProjectData>(this.apiUrl + '/' + id);
  }
  SearchProjects(ProjectName: string, ProjectStartDate:string ,ProjectEndDate:string ): Observable<any> 
  {   
    // return this.http.get(this.apiUrl+'/SearchProjectDetails'+ '?ProjectId=' + ProjectId 
    //                     + '&ProjectName=' + ProjectName
    //                     + '&ProjectPriorityFrom=' + ProjectPriorityFrom
    //                     + '&ProjectPriorityTo=' + ProjectPriorityTo
    //                     + '&ProjectStartDate=' + ProjectStartDate
    //                     + '&ProjectEndDate=' + ProjectEndDate                        

    //                 ).pipe(map((res:Response)=>res));  
    return this.http.get(this.apiUrl+'/SearchProjectDetails'+ '?ProjectName=' + ProjectName                        
                        + '&ProjectStartDate=' + ProjectStartDate
                        + '&ProjectEndDate=' + ProjectEndDate                        

                    ).pipe(map((res:Response)=>res));  


 }
  updateProject(id: number, request: any): Observable<ProjectData> {
    return this.http.put<ProjectData>(this.apiUrl + '/' + id, request);
  }

  PostProject(project: ProjectData): Observable<HttpResponse<ProjectData>> 
  {
    let httpHeaders = new HttpHeaders({
         'Content-Type' : 'application/json'
    });    
    return this.http.post<ProjectData>(this.apiUrl +'/UpdateProjectDetails', project,
        {
          headers: httpHeaders,
          observe: 'response'
        }
    );
  } 
}
