import { Component, OnInit } from '@angular/core';

import { ProjectData } from '../../DataModel/project';
import { Task } from '../../DataModel/task';
import { Observable } from "rxjs"; 
import { User } from '../../DataModel/user';
import {TaskService} from '../../Services/PMTaskServices';

import {ProjectService} from '../../Services/PMProjectServices';

import { Injectable  } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-ViewProjects',
  templateUrl: './viewprojects.component.html',
  styleUrls: ['./viewprojects.component.css']
})

//@Injectable({ providedIn: 'root'})
export class ViewProjectsComponent implements OnInit {

  projects:ProjectData[]; 
  tasks:Task[];

  projetsForSeacrh=[];   
  selectedProject: ProjectData;
  routerlocal: Router;
  
  objTaskService:TaskService; 
  objProjectService:ProjectService;
  
  dataSaved = false;
  
  searchedProjectId:any;
  searchedProjectName:any;
  searchedPriorityFrom:any;
  searchedPriorityTo:any;
  searchedStartDate:any;
  searchedEndDate:any; 

  allProjects$: Observable<ProjectData[]>;
  
  allUsers: Observable<User[]>;
  
  constructor(private router: Router,private objTaskService1:TaskService,objProjectService1:ProjectService) 
  {   
    this.routerlocal= router;  
    this.objTaskService=objTaskService1;
    this.objProjectService=objProjectService1;
    
    this.getAllUsers();
    this.getAllTasks();
   }

  ngOnInit()
  {
    
    this.getAllProjects(); 
  }

  getAllUsers(): void
  {     
    this.objTaskService.GetAllUsers()    
    .subscribe(usersnew =>this.allUsers = usersnew);     
  }
  getAllTasks(): void
  {       
    this.objTaskService.GetAllTasks()     
    .subscribe(tasksnew =>this.tasks = tasksnew);   
    
  }
  getAllProjects(): void
  {  
    this.objProjectService.getProjects()     
    .subscribe(projectsnew =>this.projects = projectsnew);       
  }

  EditProject(objProject: ProjectData)
  {
    
  }
  

  SearchProject()
  { 
      this.ClearSelectedProjectControls();     
      this.selectedProject=null;
      //this.searchedProjectId= (<HTMLInputElement>document.getElementById("projectidforsearch")).value; 

      this.searchedProjectName=(<HTMLInputElement>document.getElementById("projectnameforsearch")).value; 

   // this.searchedPriorityFrom=(<HTMLInputElement>document.getElementById("priorityFromforsearch")).value; 
    //  this.searchedPriorityTo=(<HTMLInputElement>document.getElementById("priorityToforsearch")).value;
  
      this.searchedStartDate=(<HTMLInputElement>document.getElementById("StartDateforsearch")).value; 
      this.searchedEndDate=(<HTMLInputElement>document.getElementById("EndDateforsearch")).value;  

      //this.objProjectService.SearchProjects(this.searchedProjectId,this.searchedProjectName,this.searchedPriorityFrom,this.searchedPriorityTo,this.searchedStartDate,this.searchedEndDate)      
      this.objProjectService.SearchProjects(this.searchedProjectName,this.searchedStartDate,this.searchedEndDate)      
      .subscribe(projectsnew =>this.projects = projectsnew);

  }

  ClearSelectedProjectControls()
  {
    if(this.selectedProject!=null)
    {
      this.selectedProject.ProjectId=0;
      this.selectedProject.ProjectPriority=0;
      this.selectedProject.ProjectStatus="0";
      this.selectedProject.UserId=0;  
      //this.selectedProject.TaskAssignedTo="0"; 
      
      //(<HTMLInputElement>document.getElementById("projectid")).value=""; 
     
      (<HTMLInputElement>document.getElementById("projectname")).value=""; 
      
      (<HTMLInputElement>document.getElementById("rngProjectPriority")).value="";
     
      (<HTMLInputElement>document.getElementById("projectstartdate")).value="";  
    
      (<HTMLInputElement>document.getElementById("projectenddate")).value="";  
     
      (<HTMLInputElement>document.getElementById("projectStatus")).value="0";    
     
      //(<HTMLInputElement>document.getElementById("UserId")).value="0"; 
      
      this.selectedProject=null;
    }   
  }
  CancelEdit()
  { 
    this.selectedProject=null;
  }

  ClearSearchProjects()
  {
    //(<HTMLInputElement>document.getElementById("projectidforsearch")).value=""; 
   
    (<HTMLInputElement>document.getElementById("projectnameforsearch")).value=""; 
    
    //(<HTMLInputElement>document.getElementById("priorityFromforsearch")).value=""; 
    
    //(<HTMLInputElement>document.getElementById("priorityToforsearch")).value=""; 
   
    (<HTMLInputElement>document.getElementById("StartDateforsearch")).value=""; 
    
    (<HTMLInputElement>document.getElementById("EndDateforsearch")).value="";  
    this.ClearSelectedProjectControls();  
    this.getAllProjects();  

  }

  ViewProject(values:any)
  { 
    this.ClearSelectedProjectControls();     
    this.selectedProject=null;
    this.getAllProjects();    
  }

  AddProjects(values:any)
  {   
     this.routerlocal.navigateByUrl("/project"); 
  }

  onSelect(project: ProjectData): void
  {
    this.selectedProject = project;       

  }

  EndProject(objProject: ProjectData)
  {    
    if(objProject!=null)
    {       
      objProject.ProjectStatus="Completed";
      this.objProjectService.PostProject(objProject).subscribe(
        project => {
          console.log(project);
          this.dataSaved = true;
          //this.LoadAllTasks();
          this.allProjects$ = this.objProjectService.getProjects();
          
          if( this.selectedProject!=undefined)
          { 
            alert("Project is completed");    
            this.getAllProjects();     
            this.selectedProject=null;
          } 
        },
        err => {
          console.log(err);
        }
      );         
    } 
  }


  DeleteProject(objProject: ProjectData)
  {
    if (confirm("Are you sure to delete the Project : "+objProject.ProjectName+" ?")==true)
    {     
      if(objProject!=null)
      {       
        objProject.ProjectStatus="ToBeDeleted";
        this.objProjectService.deleteProject(objProject).subscribe(
          project => {
            console.log(project);
            this.dataSaved = true;
            //this.LoadAllTasks();
            this.allProjects$ = this.objProjectService.getProjects();
           
            if( this.selectedProject!=undefined)
            { 
              alert("Project is deleted"); 
              this.ClearSelectedProjectControls();
                this.getAllProjects();  
              this.selectedProject=null;
            } 
          },
          err => {
            console.log(err);
          }
        );         
      }

      }     

    }
  

  UpdateProject(objProject: ProjectData)
  {
   
   this.objProjectService.PostProject(objProject).subscribe(
     project => {
       console.log(project);
       this.dataSaved = true;
       //this.LoadAllTasks();
       this.allProjects$ = this.objProjectService.getProjects();

       if( this.selectedProject!=undefined)
       { 
           alert("Project is saved");
             
           this.getAllProjects();    
           this.selectedProject=null;  
       }  
     },
     err => {
       console.log(err);
     }
   );
 }
}
