import { Injectable  } from '@angular/core';
import { HttpClient,HttpHeaders,HttpResponse   } from '@angular/common/http';
import { Observable, of } from 'rxjs'; 
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/toPromise';
import 'rxjs/Rx';
import { Task } from '../DataModel/task';

import { catchError, map, tap } from 'rxjs/operators';

const httpOptions = {
      
      headers: new HttpHeaders({
            //'Access-Control-Allow-Origin': 'http://DOTNET/', // -->Add this line //50020//50695
            'Access-Control-Allow-Origin': 'http://localhost:8081/',
            'Access-Control-Allow-Methods': 'GET,PUT,POST,DELETE,OPTIONS',
            'Access-Control-Allow-Headers': '*',
            'Content-Type': 'application/json',
            'Accept': 'application/json'
        })
    };

@Injectable({ providedIn: 'root'})

export class TaskService {

//For Local testing   
BaseUrl="http://localhost:8081/Api";//50020//50695
AllTasksUrl=this.BaseUrl+"/Task/AllTaskDetails";//50020//50695
AddNewTaskUrl=this.BaseUrl+"/Task/InsertTaskDetails";//50020//50695
EditTaskUrl=this.BaseUrl+"/Task/UpdateTaskDetails";//50020//50695
SearchTaskUrl=this.BaseUrl+"/Task/SearchTaskDetails";//50020//50695
AllUsersListUrl=this.BaseUrl+"/User";//50020//50695
SingleUserUrl=this.BaseUrl+"/User";//50020//50695
AllParentTasksUrl=this.BaseUrl+"/Task/GetParentTaskDetails";//50020//50695

//For Production Release
// BaseUrl="http://DOTNET/ProjectManagerWebApiService/api";
// AllTasksUrl=this.BaseUrl+"/task?type=json";//50020//50695
// AddNewTaskUrl=this.BaseUrl+"/task";//50020//50695
// EditTaskUrl=this.BaseUrl+"/task";//50020//50695
// AllUsersListUrl=this.BaseUrl+"/user?type=json";//50020//50695
// SingleUserUrl=this.BaseUrl+"/user";//50020//50695
// AllParentTasksUrl=this.BaseUrl+"/parenttask";//50020//50695

constructor(private _http: HttpClient) { }


 GetAllTasks(): Observable<any> 
 {        
       return this._http.get(this.AllTasksUrl).pipe(map((res:Response)=>res));      
 }
 GetAllParentTasks(): Observable<any> 
 {        
       return this._http.get(this.AllParentTasksUrl).pipe(map((res:Response)=>res));      
 }
 AddTask(task: Task): Observable<Task>
 {
    let httpHeaders = new HttpHeaders()
        .set('Content-Type', 'application/json');   
    let options = {
        headers: httpHeaders
    };        
    return this._http.post<Task>(this.AddNewTaskUrl, task, options);
} 

UpdateTask(task: Task): Observable<Task>
 {
    let httpHeaders = new HttpHeaders()
        .set('Content-Type', 'application/json');   
    let options = {
        headers: httpHeaders
    };        
    return this._http.post<Task>(this.EditTaskUrl, task, options);
} 

deleteTask(objTask: Task): Observable<HttpResponse<Task>> 
  {

      objTask.TaskStatus="ToBeDeleted";
      let httpHeaders = new HttpHeaders({
      'Content-Type' : 'application/json'
     });    
      return this._http.post<Task>(this.AddNewTaskUrl, objTask,
       {
            headers: httpHeaders,
           observe: 'response'
        }
      );
  }
PostTask(task: Task): Observable<HttpResponse<Task>> {
    let httpHeaders = new HttpHeaders({
         'Content-Type' : 'application/json'
    });    
    return this._http.post<Task>(this.AddNewTaskUrl, task,
        {
          headers: httpHeaders,
          observe: 'response'
        }
    );
} 


SearchTasks(TaskName: string, TaskStartDate:string,TaskEndDate: string ): Observable<any> 
{   
    return this._http.get(this.SearchTaskUrl +'?TaskName=' + TaskName                         
                        + '&TaskStartDate=' + TaskStartDate
                        + '&TaskEndDate=' + TaskEndDate
                 
                    ).pipe(map((res:Response)=>res));  

}
GetAllUsers(): Observable<any> 
{        
       return this._http.get(this.AllUsersListUrl +'/AllUserDetails').pipe(map((res:Response)=>res));      
}
GetAuthenticateUser(UserName: string, UserPassword:string):Observable<any> 
{
    return this._http.get(this.SingleUserUrl+ '?type=json&UserName=' + UserName 
             + '&UserPassword=' + UserPassword   
        ).pipe(map((res:Response)=>res)); 
}

private log(message: string)
{  

      alert("I am at the end of  the service call place -   log " + this.AllTasksUrl);
}

private handleError<T> (operation = 'operation', result?: T)
{
      return (error: any): Observable<T> => {  
       
            alert("I am at the end of  the service call place - handleError  " + this.AllTasksUrl+error.message);
       
        // TODO: send the error to remote logging infrastructure
        console.error(error); // log to console instead
  
        // TODO: better job of transforming error for user consumption
        this.log(`${operation} failed: ${error.message}`);
  
        // Let the app keep running by returning an empty result.
        return of(result as T);
      };
    }
}