import { Component, OnInit, NgProbeToken, TemplateRef } from '@angular/core';
import { Form, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { UserService } from '../../Services/PMUserServices';
import { User } from 'src/app/datamodel/user';
import { ProjectData } from 'src/app/datamodel/project';
import { Observable } from 'rxjs/Observable';
import {  FormBuilder, Validators } from '@angular/forms';
import { HttpErrorResponse } from '@angular/common/http';
import {TaskService} from '../../Services/PMTaskServices';
import { Task } from '../../DataModel/task';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {
  dataSaved = false;
  userForm: FormGroup; 
  allUsers$: Observable<User[]>;
  routerlocal: Router;
  selectedProjectFromDropdownList:any;
  selectTaskFromDropdownList:any;
  projectsForNewUser=[];   
  allProjects: ProjectData[];
  tasks:Task[];

  objTaskService:TaskService;

  constructor(private formBuilder:FormBuilder, private objUserService: UserService,objTaskService1:TaskService, private router: Router )
   { 

    this.routerlocal= router; 
    this.objTaskService=objTaskService1;
    this.getAllProjects();   
   }

  ngOnInit() {

    this.userForm = this.formBuilder.group({
      UserName: ['', [ Validators.required ] ],
      //UserPassword: ['', [ Validators.required] ],
      FirstName: ['', [ Validators.required ] ],
      LastName:  ['', [ Validators.required ] ],
      EmpId:  ['', [ Validators.required ] ],
      ProjectId: ['', [ Validators.required ] ],
      TaskId: ['', [ Validators.required ] ]     
    
    });
    

    this.objUserService.getUsers()     
    .subscribe(projectsForNewUser =>this.projectsForNewUser = projectsForNewUser);  

    this.getAllTasks();
  }

  onFormSubmit() {
    this.dataSaved = false;
    let user = this.userForm.value;
    this.objUserService.getUsers().subscribe(newusers => {
      let maxIndex = newusers.length - 1;
      let maxIndexItem = newusers[maxIndex];     
        user.UserId = 0;      
      this.CreateNewUser(user);
    });
    this.userForm.reset();
  }
  getAllProjects(): void
  {   
    
    this.objUserService.getProjects()    
    .subscribe(allProjects =>this.allProjects = allProjects);  
    
  }
  getAllTasks(): void
  {  
    this.objTaskService.GetAllTasks()     
    .subscribe(tasksnew =>this.tasks = tasksnew);       
  }
  
  CreateNewUser(objUser: User) {
    this.objUserService.addNewUser(objUser).subscribe(
      user => {    
          
        console.log(user);         
               
       if( user!=null)
       {
        this.dataSaved = true; 
        alert("User is successfully created");    
       }
       else
       {
        this.dataSaved = false; 
        alert("User is already exists"); 
       }
        //this.LoadAllUsers();
      },
      err => {
        console.log(err);
      }
    );
    this.LoadAllUsers();

  }
  LoadAllUsers() 
  {
    this.allUsers$ = this.objUserService.getUsers();
  }  
  SelectedProjectFromDropdown(selectedProjectID:any)
  {    
    this.selectedProjectFromDropdownList=selectedProjectID;
  }
  SelectedTaskFromDropdown(selectedTask:any)
  {
    this.selectTaskFromDropdownList=selectedTask;
  }

 get FirstName() 
 {
    return this.userForm.get('FirstName');
 }
 get LastName() {
    return this.userForm.get('LastName');
 }  
 get UserName() {
    return this.userForm.get('UserName');
 } 
//  get UserPassword() {
//   return this.userForm.get('UserPassword');
// } 
get EmpId() {
  return this.userForm.get('EmpId');
} 
get ProjectId() {
  return this.userForm.get('ProjectId');
} 

get TaskId() {
  return this.userForm.get('TaskId');
} 

ViewUser(values:any)
{     
    this.routerlocal.navigateByUrl("/viewusers");    
}
SaveUser() 
{
  let user:User = { UserId:2, UserName: 'Hamuman',
          FirstName: "Hanuman",
          LastName:'Ji',EmpId:'2345',
          ProjectId:2, TaskId:2
           };

     this.objUserService.PostUser(user).subscribe(res => { 
          let userObj: User = res.body;
          console.log(userObj.UserName);
          console.log(res.headers.get('Content-Type'));		
          this.LoadAllUsers();	  
        },
	(err: HttpErrorResponse) => {
          if (err.error instanceof Error) {
            //A client-side or network error occurred.				 
            console.log('An error occurred:', err.error.message);
          } else {
            //Backend returns unsuccessful response codes such as 404, 500 etc.				 
            console.log('Backend returned status code: ', err.status);
            console.log('Response body:', err.error);
          }
        }
     );
}
}

  
  


