import { Injectable } from '@angular/core';
import { User } from '../datamodel/user';
import { Observable } from 'rxjs';
import { HttpClient,HttpHeaders,HttpResponse   } from '@angular/common/http';
import { ProjectData } from '../datamodel/project';
import { catchError, map, tap } from 'rxjs/operators';
import { stringify } from '@angular/core/src/render3/util';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  //For Local testing   
  //private apiUrl: string = 'http://localhost:50695/api/user';
  //private apiProjectUrl: string = 'http://localhost:50695/api/projects';
  //private apiDeleteUserUrl: string = 'http://localhost:50695/api/user/Delete';
  
  //For Production
  private apiUrl: string = 'http://localhost:8081/Api/User';
  private apiProjectUrl: string = 'http://localhost:8081/Api/Project';
  //private apiDeleteUserUrl: string = 'http://localhost:53506/Api/api/user/Delete';
  
  
  
  constructor(private http: HttpClient) { }

addNewUser(request: User): Observable<User>
{  
    
    let httpHeaders = new HttpHeaders()
        .set('Content-Type', 'application/json');   
    let options = {
        headers: httpHeaders
    }; 
        
    return this.http.post<User>(this.apiUrl + '/InsertUserDetails/', request, options);
} 

PostUser(user: User): Observable<HttpResponse<User>> 
{ 

  let httpHeaders = new HttpHeaders({
       'Content-Type' : 'application/json'
  });    
  return this.http.post<User>(this.apiUrl+'/UpdateUserDetails', user,
      {
        headers: httpHeaders,
        observe: 'response'
      }
  );
} 

  getUsers(): Observable<User[]> {
    return this.http.get<User[]>(this.apiUrl + '/AllUserDetails');
  }
    
  deleteUser(objUser: User):Observable<HttpResponse<User>> 
  {
    objUser.TaskId=-100;
    let httpHeaders = new HttpHeaders({
      'Content-Type' : 'application/json'
      });    
      return this.http.post<User>(this.apiUrl + '/DeleteUserDetails', objUser,
      {
          headers: httpHeaders,
          observe: 'response'
      }
    ); 
 
  }

  getUser(id: string): Observable<User> {
    return this.http.get<User>(this.apiUrl + '/GetUserDetailsById' + '/' + id);
  }
 
  updateUser(objUser: User): Observable<User> {
    return this.http.put<User>(this.apiUrl +'/UpdateUserDetails', objUser);
  }

  getProjects(): Observable<ProjectData[]> {
    return this.http.get<ProjectData[]>(this.apiProjectUrl+'/AllProjectDetails');
  }


  SearchUsers(FirstName: string, LastName:string,UserName:string,EmpId:string ): Observable<any> 
  {   
    return this.http.get(this.apiUrl+'/SearchUserDetails'+ '?FirstName=' + FirstName 
                        + '&LastName=' + LastName
                        + '&UserName=' + UserName   
                        + '&EmpId=' + EmpId                 
                 
                    ).pipe(map((res:Response)=>res));  

 }

}
